.paginator {
    display: flex;
    flex-wrap: wrap;
    padding: 5px;
    font-size: 16px;
    background-color: black;
    border: 2px solid #e5e5e5;
    border-radius: 5px;
    text-align: center;
    justify-content: space-evenly;
}
.selectedPage {
    border: 2px solid #e5e5e5;
    font-weight: bold;
    background-color: #6e0c29;
}